import React, { PureComponent } from 'react';
import '../css/Person.css';
import noob from '../img/noob.png';
import whisper from '../img/whisper.png';
import atlas from '../img/atlas.png';
import hr from '../img/hr.png';

import noob_ny from '../img/noob_ny.png';
import whisper_ny from '../img/whisper_ny.png';
import atlas_ny from '../img/atlas_ny.png';
import hr_ny from '../img/hr_ny.png';

import noob_covid19 from '../img/noob_covid19.png';
import whisper_covid19 from '../img/whisper_covid19.png';
import atlas_covid19 from '../img/atlas_covid19.png';
import hr_covid19 from '../img/hr_covid19.png';

export class Person extends PureComponent {

    render() {
        let user = this.props.user;
        let persons = this.props.persons;
        let i = this.props.person;
        let ret = null;
        try{
            ret = <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
                <img className={this.props.className} src={
                    user.hasOwnProperty('active_skins') ?
                        user.active_skins.hasOwnProperty(i+'') ?
                            persons[i].skins[user.active_skins[i]]
                            :
                            persons[i].skins[0]
                        :
                        persons[i].skins[0]
                } onClick={this.props.onClick} style={{ height:"170px"}}/>
            </div>;
        }catch (e) {  }
        return (
            ret
        )
    }

}